/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { useStaticQuery, graphql } from 'gatsby';
import Image from 'gatsby-image';
import { ImageSharp } from 'src/models';
import { PaletteType, makeStyles, Theme, createStyles } from '@material-ui/core';
import { useMemo } from 'react';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
    image: {
      marginRight: '0.5rem',
    },
    text: {
      color: theme.palette.text.secondary,
      [theme.breakpoints.down('md')]: {
        display: 'none',
      },
    },
  })
);

interface QueryResult {
  logoImage: ImageSharp;
  logoWhiteImage: ImageSharp;
}

interface Props {
  size?: number;
  color?: PaletteType;
}
const LubyconLogo = ({ size = 30, color = 'light', ...rest }: Props) => {
  const styles = useStyles();
  const { logoImage, logoWhiteImage }: QueryResult = useStaticQuery(graphql`
    query LogoQuery {
      logoImage: file(absolutePath: { regex: "/logo.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      logoWhiteImage: file(absolutePath: { regex: "/logo_white.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  const logo = useMemo(() => {
    if (color === 'light') {
      return logoImage;
    } else {
      return logoWhiteImage;
    }
  }, [color, logoImage, logoWhiteImage]);

  return (
    <div className={styles.root} {...rest}>
      <Image
        css={css`
          width: ${size}px;
        `}
        fluid={logo.childImageSharp.fluid}
        className={styles.image}
        alt="Lubycon "
      />
      <span
        css={css`
          font-size: ${size / 23}rem;
        `}
        className={styles.text}
      >
        LUBYCON
      </span>
    </div>
  );
};

export default LubyconLogo;
